.modal {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.6);;
}

.paper {
    background-color: #242526;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 8px 36px rgba(0, 0, 0, 0.16);
    border-radius: 6px;
}

.modal.openModal {
    display: flex;
    justify-content: center;
    align-items: center;
    animation: modal-bg-show .3s;
}

@keyframes modal-show {
    from {
        opacity: 0;
        margin-top: -50px;
    }
    to {
        opacity: 1;
        margin-top: 0;
    }
}
@keyframes modal-bg-show {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.header {
    margin-top: 20px;
    margin-left: 24px;
    display: flex;
}

.title {
    width: 264px;
    height: 24px;
    font-family: Segoe UI;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 21px;

    color: #EFEFEF;
}

.close_btn {
    width: 24px;
    height: 24px;
    background-repeat: no-repeat;
    margin-left: auto;
    margin-right: 24px;
}

.content {
    height: 40px;
    margin-top: 28px;
    font-family: Segoe UI;
    font-style: normal;
    font-weight: 350;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #EFEFEF;
}

.bottom {
    display: flex;
    justify-content: center;

    margin-top: 28px;
}

.deny_btn {
    width: 40%;
    height: 42px;
    border: 1px solid #707070;
    box-sizing: border-box;
    border-radius: 4px;

    font-family: Segoe UI;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 40px;

    text-align: center;

    color: #EFEFEF;
}

.accept_btn {
    width: 40%;
    height: 42px;
    margin-left: 8px;

    box-sizing: border-box;
    border-radius: 4px;

    background-color: #0A84FF;

    font-family: Segoe UI;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 40px;

    text-align: center;

    color: #EFEFEF;
}